.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  /* margin: 0 auto; */
  margin-inline: 5%;
  width: 100%;
  height: 100%;
  /* overflow: hidden; */
}

.FormWrapper {
  width: 80%;
  height: 100%;
}

.logo {
  margin-block: 2rem;
  margin-inline: auto;
  height: 2rem;
  /* margin: 18px auto 8px; */
  /* position: relative; */
}

.logo img {
  width: 10rem;
}

.headers {
  display: flex;
  flex-direction: column;
  /* margin: 10px 0; */
  margin-block: 2rem;
}

.header {
  color: #000;
  font-weight: 700;
  font-size: 2.3rem;
  margin: 8px 0 5px 0;
}

.subHeader {
  font-size: 1.3rem;
  font-weight: 500;
}

.googleBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 3.5rem;
}

.googleBtn a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  /* padding: 10px; */
  outline: none;
  border: 1px solid #00b87c;
  /* border: 1px solid #1a61db; */
  font-size: 1.2rem;
  font-weight: bold;
  letter-spacing: 1px;
  color: #1a61db;
  background: transparent;
  border-radius: 3px;
  /* border-radius: 3px; */
}

.googleBtn a:hover {
  /* color: #fff;
  background: #2f80ed; */
  color: #1a61db;
  /* box-shadow: 0px 7px 20px rgba(0, 0, 0, 0.3); */
  box-shadow: 0px 7px 20px rgba(0, 0, 0, 0.25);
}

.googleBtn a:active {
  color: #fff;
  background: #00b87c;
  /* box-shadow: 0px 7px 20px -79px rgba(196, 196, 196, 0.3); */
}

.googleBtn img {
  height: 1.5rem;
  margin: 0 7px;
  padding: 2px;
  background: #fff;
}

.hrWrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #00b87c66;
  height: 2px;
  margin-block-start: 3rem;
  margin-block-end: 1.5rem;
  /* margin: 2rem 0; */
}

.hrWrapper div {
  background: white;
  padding-inline: 1rem;
}

.hrLeft,
.hrRight {
  width: 100%;
}

