.form {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
  height: auto;
}

.inputSection {
  /* display: flex; */
  /* margin: 0 auto; */
  /* flex-direction: column; */
  width: 100%;
  height: auto;
}

.btnContainer {
  width: 100%;
  padding: 10px auto;
}

.btnContainer .btn {
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 3px;
  padding-block: auto;
  padding-inline: auto;
  width: 100%;
  height: 3.5rem;
  background: #00b87c;
  color: #fff;
  font-weight: bold;
  font-size: 1.5rem;
  /* display: flex;
  align-items: center;
  justify-content: center; */
  /* letter-spacing: 1px; */
  /* background: #2f80ed; */
  /* height: 4rem; */
  /* font-weight: 700; */
  /* font-size: 16px; */
}

.btnContainer .btn:hover {
  box-shadow: 0px 7px 20px rgba(0, 0, 0, 0.25);
}

.btnContainer .btnDisabled {
  cursor: pointer;
  width: 100%;
  height: 3.5rem;
  outline: none;
  border: none;
  border-radius: 3px;
  background: #00b87c80;
  color: #fff;
  font-weight: bold;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  /* height: 4rem; */
  /* background: #00b87c; */
  /* background: #2f80ed; */
  /* font-size: 16px; */
  /* letter-spacing: 1px; */
}

.bottomLine {
  margin-block: 3rem;
  font-size: 0.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  /* font-size: 14px; */
  /* margin: 10px auto 0;
  padding: 10px; */
}

.bottomLine .bottomLink {
  color: #1a61db;
  /* color: #00b87c; */
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer a {
  margin-block: 3rem;
  margin-inline: 1.5rem;
  font-size: 0.8rem;
  color: #999999;
  /* color: rgba(0, 0, 0, 0.5); */
  /* font-size: 10px; */
  /* margin: 16px 20px; */
}

@media only screen and (max-width: 768px) {
  .container {
    width: 80%;
  }
}

@media only screen and (max-width: 468px) {
  .container {
    margin-inline: 0;
    width: 100%;
  }
}
